<template>
  <div class="wrapper">
    <parallax class="section page-header header-filter" :style="headerStyle" style="height: 700px;">
      <div class="container">
        <div class="md-layout">
    			<div class="md-layout-item md-size-100 md-small-size-100 md-xsmall-size-100 text-center">
            <router-link :to="{name: 'read', params: { url: posts.url }}" target="_blank"><h2 class="title">{{ posts.title }}</h2></router-link>
              <p class="card-description">By <strong><router-link class="text-muted" to="/profile">{{ posts.author }}</router-link></strong> | {{ posts.created_at | moment("dddd, Do MMMM YYYY") }}</p>

          </div>
          <div class=" md-layout-item md-size-100 md-small-size-100 md-xsmall-size-100 text-center" style="margin-top: 120px;">
            <!--<i class="material-icons">cloud_download</i>-->
            <md-button href="javascript:void(0)" @click="scrollToElement()" class="md-warning md-sm md-round md-just-icon"><i class='material-icons'>keyboard_arrow_down</i></md-button>
        </div>
        </div>
      </div>
    </parallax>

    <div class="main main-raised">
      <div class="section">
        <div class="container">

          <div class="md-layout">
            <div class="md-layout-item md-size-80 md-xsmall-size-80 mx-auto">

              <section>
                <div id="readSection"> </div>
              <p v-html="posts.content"></p>
            </section>

            </div>

          </div>
          <div class="sharing-area text-center">
            <div class="md-layout">
              <div class="md-layout-item md-size-50 md-xsmall-size-100 mx-auto">
                <i><h5>"Thanks for reading this article <router-link :to="{name: 'read', params: { url: posts.url }}" target="_blank">{{ posts.title }}</router-link> have a nice day :)"</h5></i>
              </div>
            </div>

            <md-button class="md-twitter"><i class="fab fa-twitter"></i>Tweet</md-button>
            <md-button class="md-facebook"><i class="fab fa-facebook-square"></i> Share</md-button>
            <md-button class="md-google"><i class="fab fa-google-plus"></i> Share</md-button>
            <md-button class="md-github"><i class="fab fa-github"></i> Star</md-button>
          </div>
        </div>
      </div>


    </div>

  </div>
</template>

<script>
import { Tabs } from "@/components";
import axios from "axios";
import 'quill/dist/quill.snow.css'


export default {
  name: "ListExpansion",
  components: {
    Tabs
  },
  bodyClass: "landing-page",
  props: {

    header: {
      type: String,
      default: require("@/assets/img/bg7.jpg")
    },
    teamImg1: {
      type: String,
      default: require("@/assets/img/faces/avatar.jpg")
    },
    teamImg2: {
      type: String,
      default: require("@/assets/img/faces/christian.jpg")
    },
    teamImg3: {
      type: String,
      default: require("@/assets/img/faces/kendall.jpg")
    },

  },
  data() {
    return {
      posts: [],
    };
  },
  created() {
      this.getPosts();
  },
  methods: {
      getPosts() {
        axios.get('http://localhost:8000/api/posp/'+ this.$route.params.url)
              .then(response => this.posts = response.data);
      },
      scrollToElement() {
        let element_id = document.getElementById("readSection");
        if (element_id) {
          element_id.scrollIntoView({ behavior: "smooth" });
        }
      }
  },
  computed: {
    headerStyle() {
      return {
        backgroundImage: `url(${this.header})`
      };
    },
    showRead() {
      const excludedRoutes = ["read"];
      return excludedRoutes.every(r => r !== this.$route.name);
    },
  }

};



</script>

<style lang="scss" scoped>
.md-card-actions.text-center {
  display: flex;
  justify-content: center !important;
}
.contact-form {
  margin-top: 30px;
}

.md-has-textarea + .md-layout {
  margin-top: 15px;
}


$list-width: 160px;

.full-control {
display: flex;
flex-direction: row;
flex-wrap: wrap-reverse;
}

.list {
width: $list-width;
}

.full-control > .md-list {
width: $list-width;
max-width: 100%;
height: 400px;
display: inline-block;
overflow: auto;
border: 1px solid rgba(#000, .12);
vertical-align: top;
}


</style>
